button {
  float: right;
  font-size: 14px;
  line-height: 18px;
  cursor: pointer;
  border: 1px solid transparent;
}
.primary__button {
  background-color: #0095f6;
  border-radius: 4px;
  color: #fff;
  padding: 5px 9px;
  font-weight: 600;
  text-align: center;
}
.text__button {
  color: #0095f6;
  font-weight: 600;
  line-height: 28px;
  background-color: transparent;
}
.app__signUp {

  display: flex;
  flex-direction: column;
  margin-top: -20px;
  margin: 28px 0;
  row-gap: 15px;
}
input {
  padding: 5px 9px;
  line-height: 18px;
  outline: none;
  border: 1px solid lightgray;
  border-radius: 3px;
}
input:focus {
  border: 1px solid gainsboro;
}
.authFooter {
  display: flex;
  flex-direction: column;
  row-gap: 15px;
}
.authFooter small {
  color: rgb(172, 172, 172);
}
.authFooter hr {
  border: 0;
  border-top: 1px solid;
  border-color: lightgray;
}
.authFooter a {
  color: #0095f6;
}